import React, { useRef, useState } from "react";
import "../register/register.css";
import { useForm } from "react-hook-form";
import Modal from "react-bootstrap/Modal";
import axios from "axios";
import { Link } from "react-router-dom";

export default function Register() {
  const [modalShow, setModalShow] = React.useState(false);
  const [errorMessage, setErrorMessage] = useState(null);
  const {
    register,
    handleSubmit,
    watch,
    formState: { errors },
  } = useForm();
  const password = useRef({});
  password.current = watch("password", "");

  const onSubmit = (data) => {
    axios
      .post(`${process.env.REACT_APP_API_BASE}/api/Auth/Registration`, data)
      .then(() => {
        setModalShow(true);
      })
      .catch((error) => {
        if (error.code === "ERR_NETWORK") {
          setErrorMessage("Nem sikerlt kapcsoldni a szerverhez.");
        } else {
          setErrorMessage(
            "A felhasznlnv vagy az e-mail cm mr regisztrlva lett a rendszerben. Krjk, hasznlj msikat."
          );
        }
      });
  };

  return (
    <div className="d-flex justify-content-center align-items-center">
      <div className="register-container justify-content-center d-flex align-items-center ">
        <div className="d-flex align-items-center flex-column">
          <div className="">
            <img
              className="register-img"
              alt="ISLANDERS"
              src="../images/islanders_logo.png"
            ></img>
          </div>

          <div className="">
            <img
              className="register-img2"
              alt="REGISZTRCI"
              src="../images/regisztracio.png"
            ></img>
          </div>

          <RegisterModal
            show={modalShow}
            onHide={() => setModalShow(false)}
          />

          <form id="form" className="" onSubmit={handleSubmit(onSubmit)}>
            <div className="justify-content-center form-group row pb-1">
              <label className="col-form-label text-center register-label">
                FELHASZNL NV
              </label>
              <input
                className="register-input"
                type="text"
                placeholder="Nv"
                id="username"
                {...register("username", {
                  required: true,
                  max: 30,
                  min: 5,
                  maxLength: 30,
                  pattern: /^(?!.*\.\.)(?!.*\.$)[^\W][\w.]+$/i,
                })}
              />
              {errors.username?.type === "required" && (
                <span className="reg-error-msg text-center">
                  Krjk adjon meg egy felhasznlnevet.
                </span>
              )}
              {errors.username?.type === "min" && (
                <span className="reg-error-msg text-center">
                  5 s 30 karakter kzti hosszsg nevet adjon meg
                </span>
              )}
              {errors.username?.type === "maxLength" && (
                <span className="reg-error-msg text-center">
                  5 s 30 karakter kzti hosszsg nevet adjon meg.
                </span>
              )}
              {errors.username?.type === "pattern" && (
                <span className="reg-error-msg text-center">
                  Krjk csak az angol ABC betit s szmokat hasznljon.
                </span>
              )}
            </div>

            <div className="justify-content-center form-group row pb-1">
              <label className="col-form-label text-center register-label">
                E-MAIL CM
              </label>
              <input
                className="register-input"
                type="text"
                placeholder="Email cm"
                id="email"
                {...register("email", {
                  required: true,
                  pattern: /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/i,
                })}
              />
              {errors.email?.type === "required" && (
                <span className="reg-error-msg text-center">
                  Krem adjon meg egy email cmet.
                </span>
              )}
              {errors.email?.type === "pattern" && (
                <span className="reg-error-msg text-center">
                  Krem adjon meg egy helyes email cmet. Pl: name@email.com
                </span>
              )}
            </div>

            <div className="justify-content-center form-group row pb-1">
              <label className="col-form-label text-center register-label">
                JELSZ
              </label>
              <input
                className="register-input"
                name="password"
                type="password"
                placeholder="Jelsz"
                id="password"
                {...register("password", {
                  required: true,
                  max: 40,
                  min: 8,
                  maxLength: 40,
                  pattern:
                    /^(?=.*\d)(?=.*[a-z])(?=.*[A-Z])(?=.*[a-zA-Z]).{8,40}$/i,
                })}
              />
              {errors.password?.type === "pattern" && (
                <span className="reg-error-msg text-center">
                  A jelsznak 8 - 40 karakter hossznak kell lennie.
                </span>
              )}
              {errors.password?.type === "pattern" && (
                <span className="reg-error-msg text-center">
                  A jelsz tartalmazzon egy kisbett, egy nagybett s egy
                  szmot.
                </span>
              )}
              {errors.password?.type === "maxLength" && (
                <span className="reg-error-msg text-center">
                  A jelsznak 8 - 40 karakter hossznak kell lennie.
                </span>
              )}
            </div>

            <div className="justify-content-center form-group row pb-1">
              <label className="col-form-label text-center register-label">
                JELSZ ELLENRZSE
              </label>
              <input
                className="register-input"
                type="password"
                placeholder="Jelsz ellenrzse"
                id="confirmPassword"
                {...register("confirmPassword", {
                  validate: (value) => value === password.current,
                })}
              />
              {errors.confirmPassword && (
                <span className="reg-error-msg text-center">
                  Nem egyezik a fent megadott jelszval!
                </span>
              )}
            </div>

            <div className="text-center">
              <span className="login-error-msg ">{errorMessage}</span>
            </div>

            <div className="d-flex justify-content-center">
              <button type="submit" className="btn btn-button3">
                Regisztrci
              </button>
            </div>
          </form>

          <div className="d-flex justify-content-center">
            <p className="ml-auto register-link">
              <a href="/">Mr regisztrlt? Lpjen be!</a>
            </p>
          </div>
        </div>
      </div>
    </div>
  );
}

function RegisterModal(props) {
  return (
    <Modal
      {...props}
      size="lg"
      aria-labelledby="contained-modal-title-vcenter"
      centered
      className="modal-animation"
    >
      <div className="successful-register-modal">
        <Modal.Header closeButton>
          <Modal.Title id="contained-modal-title-vcenter">
            Sikeres regisztrci
          </Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <p>
            Az email cmre kldtnk egy levelet a regisztrci befejezshez
            szksges linkkel. Krem kattintson r, hogy vglegestse a
            regisztrcit.
          </p>
        </Modal.Body>
        <Modal.Footer>
          <Link to="/">
            <button onClick={props.onHide} className="modal-register-btn">
              Bezrs
            </button>
          </Link>
        </Modal.Footer>
      </div>
    </Modal>
  );
}
