import React, { useContext, useEffect } from "react";
import "../tutorial/tutorial.css";
import HudContext from "../../contexts/HudContext";
import Layout from "../../components/layout/Layout";
import Modal from "react-bootstrap/Modal";
import axios from "axios";
import { Component } from "react";
import { Subject, switchMap, take, tap, timer } from "rxjs";

export default function Tutorial() {
  const { setIsHudDisplayed } = useContext(HudContext);
  const [modalShow, setModalShow] = React.useState(false);

  useEffect(() => {
    setIsHudDisplayed(true);
  }, []);

  const slotClick = () => {
    setModalShow(true);
  };

  return (
    <Layout navigations={[]} title="tmutat">
      <SlotGame show={modalShow} onHide={() => setModalShow(false)} />
      <div className="container-fluid">
        <div className="information justify-content-center">
          <span>
            <h4 className="text-center text-warning">Rvid ismertet</h4>
            <p>
              A jtk lnyege, hogy fejleszd a szigetedet. A fejlesztshez
              szksges alapanyagokat ms szigetekkel val cservel, csatval,
              vagy a sajt szigeteden indtott expedcival s a szigetedre mr
              leteleptett pletekbl termeld alapanyagokbl tudod
              beszerezni. A szintlpshez szksges tapasztalati pontokat (xp-t)
              az expedcikkal s csatkkal tudod sszegyjteni a fejldshez.
              Szintlpsenknt kapsz 3 kpessg pontot, amelyeket a szigeted
              alap pontjaihoz tudsz hozzadni, ezltal nvelheted az er,
              gyessg s intelligencia tulajdonsgait a menedzsmentben. Ezeket
              alul rszleteznk. Amennyiben szksges menetkzben a gyors
              informci, a krdjel ikonokra kattintva tudhatsz meg tbbet.
            </p>

            <h4 className="text-center text-warning">Szigetek</h4>
            <p>
              Ngy alap sziget van a jtkban: Eurpai, japn, viking s indin.
              Mindegyikk ms alapstatisztikval rendelkezik s ms alapanyag
              kvetelmnyekkel a fejldshez.
            </p>

            <h4 className="text-center text-warning">A jtk ngy f rsze</h4>
            <br></br>
            <p>
              - Expedci a sajt szigeten,<br></br>- Csata ms szigetek ellen,
              <br></br>- Szigetek kztti cserekereskedelem,<br></br>- F
              pletek ptse s fejlesztse.<br></br>
            </p>

            <h4 className="text-center text-warning">Az expedci</h4>
            <p>
              Az expedcit mr a jtk elejn el tudod indtani. Kt expedci
              kztt 1 perc vrakozsi id van, ezalatt nem indthatsz jat. Az
              expedci sikeressgnek eredmnyt, a begyjttt alapanyagokkal
              egytt azonnal megkapod. Hrom nehzsgi fokozat kzl
              vlaszthatsz, termszetesen a legknnyebb adja a legkevesebb
              anyagot s tapasztalati pontot, a legnehezebb a legtbbet. A
              sikeressgk kimenetele ugyangy a nehzsgtl fggen vltozik.
              Az ts szintig ez az egyetlen lehetsg az xp gyjtsre a
              szintlpsekhez. Az expedcin kapott nyersanyagot befolysolja
              mg az intelligencia mennyisge is.
            </p>

            <h4 className="text-center text-warning">A csata</h4>
            <p>
              Csatzni az tdik szint elrse utn lesz lehetsged.
              Vletlenszeren kisorsolt ellenfeleket dob fel a jtk s azokat
              tudod majd megtmadni. Legfeljebb 5 ellensges sziget kzl
              vlaszthatsz a csatk sorn. A rendszer a te szintedhez mrten az
              egy szinttel alacsonyabbaktl a kt szinttel magasabbakig adja be
              az ellenfeleket, hogy ne legyen tl nagy klnbsg az
              erviszonyokban. A csatkat befolysoljk az albbi tnyezk:
              templom szint, kikpz szint, gyessg / er / intelligencia
              mennyisge. Alul pontosabb informcikat kaphatsz rla, valamint
              az adott menben (fejlesztsnl, ptsnl s tapasztalati
              pontnl) a krdjelre kattintva ott is tbb informcihoz
              juthatsz.
            </p>

            <h4 className="text-center text-warning">A kereskedelem</h4>
            <p>
              A kereskedelem a tbbi szigettel a piac menpont alatt zajlik. Itt
              tallhatod a jtkosok ltal feladott hirdetseket. A tbbi sziget
              ltal kirakott portkt az sszes hirdets gombra kattintva
              tekintheted meg s bngszheted kedved szerint. A hirdets
              feladsa gombra kattintva tudsz felrakni sajt csere ajnlatokat.
              A feladott hirdetseidet a sajt hirdetsek gombra kattintva fogod
              tudni elrni s trlni, ha meggondolnd magadat. A felknlt
              portka a hirdets feladsakor levonsra kerl a kszletedbl.
              Amint valaki e csert elfogadja, a vrt ttel jv rdik a te
              alapanyagaid kztt. Amennyiben te fogadsz el egy hirdetst, a
              csere azonnal megtrtnik.
            </p>

            <h4 className="text-center text-warning">Fejleszts s pts</h4>
            <p>
              t alap pletet tudsz lerakni s azokat tudod fejleszteni.
              Mindegyiknek megvan a sziget fajtjtl fggen az alapanyag
              kvetelmnye a megptshez s fejlesztshez. Termszetesen tbb
              anyag szksges a msodik s harmadik szintre val fejlesztshez,
              mint az els szint megptshez. Te magad dntd el, hogy a
              kijellt terletek kzl melyik helyre rakod le az adott pletet
              s hogy milyen sorrendben. Mindegyik plet termel passzvan
              alapanyagot s megptskor / fejlesztskor ad egy kevs XP-t.
              <br></br>
              <br></br>
              <span className="text-warning">Az t alap plet:</span>
              <br></br>- <b>Templom:</b> <br></br>
              Passzvan pnzt termel. Csatban plusz sebzst okoz szzalkos
              arnyban. Szinttl fggen tbb a sebzs s a termelt pnz
              mennyisge.
              <br></br>- <b>Kikpz:</b> <br></br>
              Passzvan pnzt termel. Csatban a kritikus tallat sebzst
              nveli. Szinttl fggen tbb a sebzs kritikus tmads esetn s
              a termelt pnz mennyisge.
              <br></br>- <b>Fa termel:</b> <br></br>
              Passzvan ft termel. Szinttl fggen emelkedik a termelt fa
              mennyisge.
              <br></br>- <b>K termel:</b> <br></br>
              Passzvan kvet termel. Szinttl fggen emelkedik a termelt k
              mennyisge.
              <br></br>- <b>Vas termel:</b> <br></br>
              Passzvan vasat termel. Szinttl fggen emelkedik a termelt vas
              mennyisge.
              <br></br>
            </p>

            <h4 className="text-center text-warning">Kpessgek</h4>
            <p>
              A kpessgek elg fontos szerepet tltenek be a szigetek letben.
              Csatban sokkal nagyobb sebzshez juthatsz, illetve tbb anyagot
              szerezhetsz ltaluk. Van maximum rtkk, gy nem rakhatsz minden
              pontot egy gra.
              <br></br>
              <br></br>- <b>Er:</b> <br></br>
              Nveli a bevitt sebzs mrtkt.
              <br></br>- <b>gyessg:</b> <br></br>
              Nveli a kritikus tallat eslyt.
              <br></br>- <b>Intelligencia:</b> <br></br>
              Nveli a sikeres expedci s csata utni alapanyag s xp
              mennyisgt.
            </p>

            <h4 className="text-center text-warning">Alapanyagok</h4>
            <p>
              Hrom f alapanyag van a fa, k, s a vas. Ezek mellett van mg XP
              s pnz. A pnz, fa, k s vas szksgesek az pletek
              megptshez s fejlesztshez, az XP pedig a sziget ssz
              szintjnek nvelshez, amely a plusz pontokat adja a
              kpessgekhez a fejldsben.
            </p>

            <h4 className="text-center text-warning">Vgsz</h4>
            <p>
              Remljk lvezni fogod a jtkot! Amennyiben olyan krdsed
              merlne fel, mely nem kerlt megvlaszolsra az albbi email
              cmeken zenhetsz neknk:
            </p>
          </span>
          <div className="makers d-flex justify-content-evenly">
            <div>
              <img
                src="../images/makers/MszrosBalzs.jpg"
                className="card-img-fluid"
                alt="Balzs"
                title="Mszros Balzs"
                onClick={slotClick}
              ></img>
              <div className="tcard-body">
                <h5 className="tcard-title">Fejleszt</h5>
                <h6 className="tcard-name">Mszros Balzs</h6>
                <p className="tcard-text">
                  <a href="mailto:meszarosb1@kkszki.hu">meszarosb1@kkszki.hu</a>
                </p>
              </div>
            </div>
            <div>
              <img
                src="../images/makers/LeknerNorbert.jpg"
                className="card-img-fluid"
                alt="Norbert"
                title="Lekner Norbert"
              ></img>
              <div className="tcard-body">
                <h5 className="tcard-title">Fejleszt</h5>
                <h6 className="tcard-name">Lekner Norbert</h6>
                <p className="tcard-text">
                  <a href="mailto:leknern@kkszki.hu">leknern@kkszki.hu</a>
                </p>
              </div>
            </div>
            <div>
              <img
                src="../images/makers/SzigiliEdit.jpg"
                className="card-img-fluid"
                alt="Edit"
                title="Szigili Edit"
              ></img>
              <div className="tcard-body">
                <h5 className="tcard-title">Fejleszt</h5>
                <h6 className="tcard-name">Szigili Edit</h6>
                <p className="tcard-text">
                  <a href="mailto:szigilie@kkszki.hu">szigilie@kkszki.hu</a>
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </Layout>
  );
}

const SLOT_VALUES = [
  "../images/makers/LeknerNorbert.jpg",
  "../images/makers/MszrosBalzs.jpg",
  "../images/makers/SzigiliEdit.jpg",
  "../images/profile_pictures/viking_profile.png",
  "../images/profile_pictures/indian_profile.png",
  "../images/profile_pictures/europian_profile.png",
];

class SlotGame extends Component {
  constructor(props) {
    super(props);

    this.ITEMS = [
      "../images/makers/LeknerNorbert.jpg",
      "../images/makers/MszrosBalzs.jpg",
      "../images/makers/SzigiliEdit.jpg",
    ];

    this.state = {
      firstSlot: this.ITEMS[0],
      secondSlot: this.ITEMS[1],
      thirdSlot: this.ITEMS[2],
      result: 0,
      isFinished: false,
    };

    this.imgPath = [
      "../images/slot/slot_lose.png",
      "../images/slot/slot_win.png",
      "../images/slot/slot_jackpot.png",
    ];

    this.spins$ = new Subject();
  }

  setSlots(firstSlot, secondSlot, thirdSlot) {
    this.setState((state) => ({
      ...state,
      firstSlot: firstSlot,
      secondSlot: secondSlot,
      thirdSlot: thirdSlot,
    }));
  }

  setResult(result) {
    this.setState((state) => ({
      ...state,
      result: result,
    }));
  }

  setIsFinished(isFinished) {
    this.setState((state) => ({
      ...state,
      isFinished: isFinished,
    }));
  }

  gameCost() {
    axios
      .put(`${process.env.REACT_APP_API_BASE}/api/Player/CollectRewards`, {
        coins: -10,
        woods: 0,
        stones: 0,
        irons: 0,
      })
      .catch((error) => {
        console.log(error);
      })
      .finally(() => {});
  }

  winPost() {
    axios
      .put(`${process.env.REACT_APP_API_BASE}/api/Player/CollectRewards`, {
        coins: 20,
        woods: 0,
        stones: 0,
        irons: 0,
      })
      .catch((error) => {
        console.log(error);
      })
      .finally(() => {});
  }

  jackpotPost() {
    axios
      .put(`${process.env.REACT_APP_API_BASE}/api/Player/CollectRewards`, {
        coins: 20,
        woods: 10,
        stones: 10,
        irons: 10,
      })
      .catch((error) => {
        console.log(error);
      })
      .finally(() => {});
  }

  spin(isLast) {
    const newSlot1 =
      SLOT_VALUES[Math.floor(Math.random() * SLOT_VALUES.length)];
    const newSlot2 =
      SLOT_VALUES[Math.floor(Math.random() * SLOT_VALUES.length)];
    const newSlot3 =
      SLOT_VALUES[Math.floor(Math.random() * SLOT_VALUES.length)];
    this.setSlots(newSlot1, newSlot2, newSlot3);

    if (!isLast) return;
    this.setIsFinished(true);

    if (newSlot1 === newSlot2 && newSlot2 === newSlot3) {
      this.setResult(2);
      this.jackpotPost();
    } else if (
      newSlot1 === newSlot2 ||
      newSlot2 === newSlot3 ||
      newSlot1 === newSlot3
    ) {
      this.setResult(1);
      this.winPost();
    } else {
      this.setResult(0);
    }
  }

  componentDidMount() {
    this.spins$
      .pipe(
        tap(() => this.setIsFinished(false)),
        switchMap(() => timer(0, 200).pipe(take(10)))
      )
      .subscribe((count) => {
        this.spin(count === 9);
      });
  }

  render() {
    return (
      <Modal
        {...this.props}
        size="lg"
        aria-labelledby="contained-modal-title-vcenter"
        centered
      >
        <div className="modal-game-container">
          <Modal.Body>
            <div className="">
              <div className="winnings-img text-center">
                {this.state.isFinished ? (
                  <img
                    src={this.imgPath[this.state.result]}
                    alt="eredmny"
                  ></img>
                ) : null}
              </div>
              <div className="d-flex justify-content-center align-items-center">
                <div className="slot-machine d-flex justify-content-center align-items-center">
                  <div className="slotbg">
                    <img src={this.state.firstSlot} alt="slot1"></img>
                  </div>
                  <div className="slotbg p-5">
                    <img src={this.state.secondSlot} alt="slot2"></img>
                  </div>
                  <div className="slotbg">
                    <img src={this.state.thirdSlot} alt="slot3"></img>
                  </div>
                </div>
              </div>
              <div className="result-container d-flex justify-content-center">
                <button
                  className="slot-btn"
                  onClick={() => {
                    this.spins$.next();
                    this.gameCost();
                  }}
                ></button>
                <button
                  className="close-slot-btn font-btn"
                  onClick={this.props.onHide}
                >
                  Bezrs
                </button>
              </div>
            </div>
            <div className="text-center text-warning pt-1">
              <span>10 Coin / prgets</span>
              <br></br>
              <span>Vesztsnl: 0 loot | Nyersnl: 20 coin</span>
              <br></br>
              <span>Jackpot esetn 10db minden anyagbl</span>
            </div>
          </Modal.Body>
        </div>
      </Modal>
    );
  }
}
