import React from "react";
import Modal from "react-bootstrap/Modal";

import "./BattleConfirmDialog.css";

export default function BattleConfirmDialog({ close, attack }) {
  return (
    <>
    <Modal show={true} centered>
      <Modal.Header className="border-0">
        <Modal.Title className="text-white"><span>Csata megerstse</span></Modal.Title>
      </Modal.Header>
      <Modal.Body className="text-white"><span>Biztosan megtmadja a kivlasztott szigetet?</span></Modal.Body>
      <Modal.Footer className="border-0">
        <button className="battle-confirm-button font-btn text-white" onClick={() => close()}>Mgse</button>
        <button className="battle-confirm-button font-btn text-white" onClick={() => attack()}>Tmad</button>
      </Modal.Footer>
    </Modal>
    </>
  );
}